<template>
  <div class="singlePage secondary-page-mode">
     <div class="secondary-toolbar">
          <span>时间选择 : </span>&nbsp;
          <div class="layoutBox" style="width:380px">
            <el-date-picker
              v-model="dateRange"
              type="datetimerange"
              align="left"
              format = 'yyyy-MM-dd HH:mm:ss'
              value-format = 'yyyy-MM-dd HH:mm:ss'
              start-placeholder="开始日期"
              end-placeholder="结束日期">
            </el-date-picker>
          </div>&emsp;&emsp;
          <span>监测项目 : </span>&nbsp;
          <div class="layoutBox" style="width:187px">
            <el-select v-model="factorModel" multiple collapse-tags placeholder="请选择" @change="selectChange">
                  <el-option
                    v-for="item in factorData"
                    :key="item.projectCode"
                    :label="item.projectName"
                    :value="item.projectCode">
                  </el-option>
            </el-select>
          </div>&emsp;&emsp;
          <el-button @click="getEchartData"><i class="iconfont icon-sousuo"></i>查询</el-button>
        
     </div>
     <div style="position:relative" class="currency-mode-wrap"  v-loading="loading"  element-loading-text="加载中"
               element-loading-spinner="loading-type-a"
               element-loading-background="rgba(255, 255, 255, 1)">
           <div class="echartbox" id="echartbox">

           </div>
            <yf-Datastatus :dataStatus="dataStatus" v-if="dataStatus == 'no'"></yf-Datastatus>
     </div>
  </div>
</template>

<script>
export default {
  name: "",
  data() {
    return {
      dateRange: [],
      factorModel: [],
      factorData: [],
      loading: true,
      colors: {
        w01010: "#e53ce6", //水温
        w01009: "#297eff", //溶解氧
        w01003: "#1cad8b", //浑浊度
        w01014: "#00a3f5", //电导率
        w01019: "#1ebd1e", //高锰酸盐指数
        w21003: "#c2a100", //氨氮
        w21011: "#e68320", //总磷
        w21001: "#ff4242", //总氮
        w01001: "#8e42ff", //PH
        w01018: "#4258ff" //CODcr
      },
      unit: {
        水温: "℃", //水温
        溶解氧: "mg/L", //溶解氧
        浑浊度: "NTU", //浑浊度
        电导率: "μs/cm", //电导率
        高锰酸盐指数: "mg/L", //高锰酸盐指数
        氨氮: "mg/L", //氨氮
        总磷: "mg/L", //总磷
        总氮: "mg/L", //总氮
        PH: "无量纲", //PH
        CODcr: "mg/L" //CODcr
      },
      legendData: [],
      xAxisData: [],
      yAxisData: [],
      seriesData: [],
      dataStatus: ""
    };
  },
  created() {
    this.initTime();
  },
  mounted() {},
  methods: {
    initEchart(legendData, xAxisData, yAxisData, seriesData) {
      let _this = this;
      var option = {
        title: {
          text: "变化趋势分析图",
          x: "center"
        },
        tooltip: {
          trigger: "axis",
          formatter: function(params) {
            let res = params[0].axisValue + "<br/>";
            for (let i = 0; i < params.length; i++) {
              res +=
                "<p>" +
                params[i].seriesName +
                " : " +
                params[i].value +
                "" +
                _this.unit[params[i].seriesName] +
                "</p>";
            }
            return res;
          }
        },
        legend: {
          data: legendData,
          y: "bottom"
        },
        toolbox: {
          show: true,
          feature: {
            dataZoom: {
              yAxisIndex: "none"
            },
            magicType: { type: ["line", "bar"] },
            restore: {},
            saveAsImage: {}
          }
        },
        xAxis: {
          type: "category",
          boundaryGap: false,
          data: xAxisData
        },
        yAxis: yAxisData,

        series: seriesData
      };
      //初始化echarts实例
      var myChart = this.$echarts.init(document.getElementById("echartbox"));

      //使用制定的配置项和数据显示图表
      myChart.setOption(option);
      myChart.on("click", function(params) {
        console.log(params);
      });
    },
    selectChange(arr) {},
    initTime() {
      //初始化时间
      let startTime = new Date().setDate(new Date().getDate() - 3);
      let endTime = new Date();
      this.dateRange = [
        new Date(startTime).format("yyyy-MM-dd hh:mm:ss"),
        endTime.format("yyyy-MM-dd hh:mm:ss")
      ];
    },
    //获取所有监测项目
    getFactor() {
      this.$http.post("/SiteManagement/index/query").then(res => {
        if (res.data.code == 0) {
          this.factorModel = [];
          this.factorData = res.data.content.dataList;
          for (let i in this.factorData) {
            this.factorModel.push(this.factorData[i].projectCode);
          }
          this.getEchartData();
        }
      });
    },
    getEchartData() {
      this.loading = true;
       this.dataStatus=''
      this.$http
        .get("/StatisticsAndAlarm/dataCharts/moreDatasCharts", {
          params: {
            stationCode: this.stationId, //站点编码（mn）'40000000000001'
            factorCode: this.factorModel.join(","), //因子参数（编码）'w01019'
            startTime: this.dateRange[0], //开始时间
            endTime: this.dateRange[1], //结束时间
            type: "hour"
          }
        })
        .then(res => {
             this.loading = false;
            if(!res.data.content){
                var myChart = this.$echarts.init(document.getElementById("echartbox"));

                //使用制定的配置项和数据显示图表
                myChart.clear();
                this.dataStatus='no';
                return false;
            }else{
              this.dataStatus='';
            }
          let obj = res.data.content.info;
          this.legendData = obj.legend;
          this.xAxisData = obj.xAxis;
         
           let yArr = res.data.content.info.series;
          if (yArr && yArr.length > 0) {
           
            for (let i = 0; i < yArr.length; i++) {
              //根据因子数量，添加Y轴，Y轴分布为左右两边最多各五条
              if (i < 5) {
                this.yAxisData.push({
                  type: "value",
                  name: "",
                  position: "left",
                  offset: 35 * i,
                  axisLine: {
                    lineStyle: {
                      color: this.colors[yArr[i].code]
                    }
                  }
                });
              } else {
                this.yAxisData.push({
                  type: "value",
                  name: "",
                  position: "right",
                  offset: 35 * (i - 5),
                  axisLine: {
                    lineStyle: {
                      color: this.colors[yArr[i].code]
                    }
                  }
                });
              }
              //根据因子数添加绘图数据seriesData
              this.seriesData.push({
                name: yArr[i].name,
                type: "line",
                data: yArr[i].data,
                yAxisIndex: i,
                itemStyle: {
                  color: this.colors[yArr[i].code]
                },
                markPoint: {
                  data: [
                    { type: "max", name: "最大值" },
                    { type: "min", name: "最小值" }
                  ]
                }
              });
            }
            this.initEchart(
              this.legendData,
              this.xAxisData,
              this.yAxisData,
              this.seriesData
            );
          }
        });
    }
  },
  computed: {
    stationInfo: {
      get: function() {
        return this.$store.state.stationInfo;
      }
    },
    detailTree: {
      get: function() {
        return this.$store.state.detailTree;
      }
    }
  },
  watch: {
    stationInfo(value) {
      if (value.length > 0) {
        this.stationId = value[0].nodeId;
        this.getFactor();
      }
    },
    detailTree(value) {
      //监听站点树开启关闭重新绘图
      var myChart = this.$echarts.init(document.getElementById("echartbox"));
      setTimeout(() => {
        myChart.resize();
      }, 300);
    }
  }
};
</script>

<style scope lang="less">
.singlePage {
  .echartbox {
    margin-top: 5px;
    height: 450px;
  }
}
</style>



